import * as PIXI from "pixi.js";

/**
 *
 */
export class ObjectBase extends PIXI.Container {

  /**
   *
   * @type {Object}
   * @protected
   */
  protected _resources: PIXI.IResourceDictionary

  /**
   * Is the this one facing right?
   * @type {boolean}
   * @protected
   */
  protected _isFacingRight: boolean = true;

  /**
   * the object velocity x
   * @type {number}
   * @protected
   */
  protected _vx = 0;

  /**
   * the object velocity y
   * @type {number}
   * @protected
   */
  protected _vy = 0;

  /**
   * the objects rotation velocity
   * @type {number}
   * @private
   */
  protected _vr = 0;

  /**
   *
   * @param resources
   */
  constructor(
    resources: PIXI.IResourceDictionary
  ) {
    super();

    this._resources = resources;
  }

  get isFacingRight(): boolean {
    return this._isFacingRight
  }

  /**
   *
   * @returns {number}
   */
  get vx() {
    return this._vx;
  }

  /**
   *
   * @returns {number}
   */
  get vy() {
    return this._vy;
  }

  /**
   *
   * @returns {number}
   */
  get vr() {
    return this._vr;
  }

  /**
   *
   * @returns {Number}
   */
  get realx() {
    return this.x - (Math.abs(this.width) / 2);
  }

  /**
   *
   * @param actor
   * @public
   */
  public collides(actor: ObjectBase) {

    const collides = !(
      actor.x > (this.realx + Math.abs(this.width)) ||
      (actor.x + Math.abs(actor.width)) < this.realx ||
      actor.y > (this.y + Math.abs(this.height)) ||
      (actor.y + Math.abs(actor.height)) < this.y
    );

    if (collides) {
      //console.log("p width", this.width);
      //console.log("actor width", actor.width);
    }

    return collides;
  }
}